rm:translateY(-50%);width:8px;height:8px;border-radius:50%;background:SteelBlue}.calendar__item--disabled{background:rgba(0,0,0,0.2)}.calendar__item--active{background:#20B2AA}.calendar__item__day-week{text-align:end}.calendar__item__day{line-height:32px;font-size:32px}.new-idea-item{position:fixed;padding:40px;width:40%;right:0;bottom:0;top:0;border-left:1px solid black;background:white;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;transition:0.3s}@media screen and (max-width: 1300px){.new-idea-item{width:100%}}.new-idea-item__close{position:absolute;top:20px;right:20px;display:inline;font-size:32px}.new-idea-item__input{border-bottom:1px solid gray;padding:8px;margin-bottom:24px}.new-idea-item__input--title{height:36px}.new-idea-item__input--text{resize:none;height:200px}.new-idea-item__input__todos{grid-column:1 / 3}.new-idea-item__set-time{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-between;justify-content:space-between}.new-idea-item__buttons{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;margin-top:auto}.new-idea-item__button{width:86px;height:48px;border-radius:8px;font-size:24px;transition:0.2s}.new-idea-item__button--save{background:black;color:white}.new-idea-item__button--delete{background:red}.header{display:-webkit-flex;display:flex;grid-gap:24px;gap:24px;padding:32px;-webkit-justify-content:space-between;justify-content:space-between}.header__nav{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;width:270px;-webkit-justify-content:space-between;justify-content:space-between;font-size:22px}.header__button{position:relative;min-width:48px;height:48px;background:LightSeaGreen;border:4px solid Teal;border-radius:50%;padding:10px}.header__button--today{border-radius:8px;width:164px}.header__button--add-todo{width:20px}.header__button__arrow{width:12px}.header__button__a